.layout-splitter {
  height: 100vh;
}

.main-content {
  padding: 20px;
  overflow: auto;
}

@media (max-width: 768px) {
  .layout-splitter {
    flex-direction: column;
  }

  .main-content {
    padding: 10px; // Reduce padding on mobile
    height: 100%;
    overflow: hidden; // Prevent scrolling issues
  }
}

.flutter-layout {
  height: calc(var(--vh, 1vh) * 100);
}
